"use client"

import { useRef, useEffect, useState } from "react"

interface MusicPlayerProps {
  isActive: boolean
  onAudioRef?: (audio: HTMLAudioElement | null) => void
}

export function MusicPlayer({ isActive, onAudioRef }: MusicPlayerProps) {
  const audioRef = useRef<HTMLAudioElement>(null)
  const [isPlaying, setIsPlaying] = useState(false)

  useEffect(() => {
    if (onAudioRef && audioRef.current) {
      onAudioRef(audioRef.current)
    }
  }, [onAudioRef, isActive])

  useEffect(() => {
    if (isActive && audioRef.current) {
      const timer = setTimeout(() => {
        audioRef.current?.play().catch(() => {})
        if (onAudioRef && audioRef.current) {
          onAudioRef(audioRef.current)
        }
      }, 100)
      return () => clearTimeout(timer)
    }
  }, [isActive, onAudioRef])

  useEffect(() => {
    const audio = audioRef.current
    if (!audio) return

    const handlePlay = () => setIsPlaying(true)
    const handlePause = () => setIsPlaying(false)

    audio.addEventListener("play", handlePlay)
    audio.addEventListener("pause", handlePause)

    return () => {
      audio.removeEventListener("play", handlePlay)
      audio.removeEventListener("pause", handlePause)
    }
  }, [])

  if (!isActive) return null

  return (
    <>
      <audio ref={audioRef} src="/music.mp3" loop preload="auto" />

      {/* Now Playing - Bottom Right above audio controls */}
      <div className="fixed right-4 bottom-20 z-40">
        <div className="glass rounded-lg px-4 py-3 backdrop-blur-md">
          <div className="flex items-center gap-3">
            {isPlaying && (
              <div className="flex items-center gap-0.5">
                <span className="w-0.5 h-3 bg-accent rounded-full animate-pulse" style={{ animationDelay: "0ms" }} />
                <span className="w-0.5 h-4 bg-accent rounded-full animate-pulse" style={{ animationDelay: "150ms" }} />
                <span
                  className="w-0.5 h-2.5 bg-accent rounded-full animate-pulse"
                  style={{ animationDelay: "300ms" }}
                />
                <span
                  className="w-0.5 h-3.5 bg-accent rounded-full animate-pulse"
                  style={{ animationDelay: "450ms" }}
                />
              </div>
            )}
            <div className="text-xs">
              <p className="text-foreground font-medium">Believer</p>
              <p className="text-muted-foreground">Imagine Dragons</p>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}
